
.find {
  height: 100%;
}
.find .header-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}
.find .find-content {
  box-sizing: border-box;
  width: 100vw;
  height: 100%;
  padding: 55px 0;
  overflow: hidden;
}
.find .find-content .find-panel {
  width: calc(100vw * 5);
  font-size: 0;
  transition: all 1s cubic-bezier(0.5, 0.5, 0.5, 1.08);
}
.find .find-content .find-panel .card-wrapper {
  display: inline-block;
  margin: 6vw 0 0 6vw;
}
.find .find-random {
  position: fixed;
  bottom: 80px;
  width: 100%;
  height: 30px;
  text-align: center;
  z-index: 100;
}
.find .find-random .random-btn {
  width: 100px;
  height: 30px;
  line-height: 30px;
  margin: 0 auto;
  border: 1px solid #1e8ae8;
  border-radius: 2px;
  font-size: 16px;
  color: #000;
  font-weight: 700;
  color: #1e8ae8;
}


