
.write-topic {
  position: relative;
  width: 100%;
  height: 100%;
}
.write-topic .header-wrapper {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
.write-topic .content-wrapper {
  box-sizing: border-box;
  padding: 100px 20px 0px 20px;
  height: 100%;
}
.write-topic .content-wrapper .message-title {
  width: 100%;
}
.write-topic .content-wrapper .message-title input {
  width: 100%;
  height: 30px;
  margin-bottom: 10px;
  border-bottom: 1px solid #ccc;
  outline: none;
  font-size: 20px;
}
.write-topic .content-wrapper .message-label {
  font-size: 0;
  padding-bottom: 10px;
}
.write-topic .content-wrapper .message-label span {
  display: inline-block;
  width: 60px;
  height: 30px;
  margin-right: 10px;
  border-radius: 4px;
  background: #f8f8f8;
  line-height: 30px;
  font-size: 16px;
  text-align: center;
  color: #7d8b93;
}
.write-topic .content-wrapper .message-label span.active {
  box-shadow: 0 0 3px 1px #ccc;
  background: #1e8ae8;
  color: #fff;
}
.write-topic .content-wrapper .message-content {
  height: 50%;
}
.write-topic .content-wrapper .message-content textarea {
  width: 100%;
  height: 100%;
  outline: none;
  font-size: 16px;
  outline: none;
  resize: none;
  border: 0;
  line-height: 20px;
}
.write-topic .message-go {
  position: fixed;
  top: 0;
  right: 0;
  width: 80px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  font-size: 20px;
  color: #fff;
}


