
.card {
  box-sizing: border-box;
  width: 41vw;
  height: 220px;
  padding: 20px;
  border: 1px solid #1e8ae8;
  border-radius: 4px;
  position: relative;
  box-shadow: 0 0 4px 0px #ccc;
}
.card .card-avatar {
  text-align: center;
}
.card .card-avatar img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 0 auto;
}
.card .card-username {
  margin: 10px 0;
  font-size: 18px;
  font-weight: 800;
  color: #000;
  text-align: center;
}
.card .card-username span {
  font-size: 20px;
  font-weight: 800;
}
.card .card-value {
  font-size: 0px;
  text-align: center;
  margin: 10px;
}
.card .card-value span {
  font-size: 16px;
  color: #1e8ae8;
}
.card .card-info {
  width: 80%;
  height: 30px;
  margin: 0 auto;
  line-height: 30px;
  font-size: 0;
  color: #1e8ae8;
  text-align: center;
  border: 1px solid #1e8ae8;
  border-radius: 2px;
}
.card .card-info i {
  line-height: 30px;
  font-size: 16px;
}
.card .card-info span {
  line-height: 30px;
  vertical-align: top;
  font-size: 20px;
}


