
.rating {
  padding: 16px;
  margin-bottom: 8px;
  border-top: 1px solid #eaecf0;
  border-bottom: 1px solid #e3e3e4;
  box-shadow: 0 1px 2px 1px #e3e3e4;
  background: #fff;
}
.rating .user {
  font-size: 0;
}
.rating .user img {
  border-radius: 50%;
}
.rating .user span {
  display: inline-block;
  height: 20px;
  margin-left: 8px;
  line-height: 26px;
  color: #919da4;
  font-size: 12px;
  vertical-align: top;
}
.rating .markdown-body {
  margin: 8px 0;
}
.rating .rating-footer:after,
.rating .rating-footer:before {
  content: '';
  display: table;
  overflow: hidden;
  clear: both;
}
.rating .rating-footer .create-time {
  float: left;
  font-size: 14px;
  color: #788790;
}
.rating .rating-footer .up {
  float: right;
  font-size: 0;
  color: #788790;
  line-height: 1;
}
.rating .rating-footer .up i {
  display: inline-block;
  font-size: 15px;
}
.rating .rating-footer .up i.active {
  color: #1e8ae8;
}
.rating .rating-footer .up span {
  display: inline-block;
  margin-left: 10px;
  font-size: 15px;
  vertical-align: top;
}


