
.topic {
  height: 100%;
}
.topic .header-wrapper {
  position: fixed;
  left: 0;
  top: 0;
}
.topic .topic-content {
  box-sizing: border-box;
  padding-top: 56px;
  height: 100%;
  overflow: auto;
}
.topic .topic-content .topic-main {
  padding: 15px;
}
.topic .topic-content .topic-main .user {
  font-size: 0;
}
.topic .topic-content .topic-main .user img {
  display: inline-block;
  border-radius: 50%;
}
.topic .topic-content .topic-main .user span {
  display: inline-block;
  margin-left: 12px;
  font-size: 12px;
  color: #747474;
  font-weight: 800;
  line-height: 26px;
  vertical-align: top;
}
.topic .topic-content .topic-main h2 {
  margin: 13px 0;
  font-size: 20px;
  font-weight: 900;
  color: #212121;
  line-height: 25px;
}
.topic .topic-content .topic-main .topic-main-footer {
  color: #b9b9b9;
  font-size: 0;
  line-height: 1;
}
.topic .topic-content .topic-main .topic-main-footer span {
  font-size: 16px;
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  margin-right: 30px;
}
.topic .topic-content .topic-main .topic-main-footer i {
  font-size: 16px;
  display: inline-block;
}
.topic .topic-content .goRating {
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #c5c7ca;
  margin-top: 6px;
  color: #919da4;
  font-size: 0;
  text-align: center;
  line-height: 50px;
  box-shadow: 1px 1px 1px #dfe1e4;
}
.topic .topic-content .goRating .left,
.topic .topic-content .goRating .right {
  -ms-flex: 1;
      flex: 1;
}
.topic .topic-content .goRating .left i,
.topic .topic-content .goRating .right i {
  display: inline-block;
  font-size: 15px;
  line-height: 50px;
}
.topic .topic-content .goRating .left span,
.topic .topic-content .goRating .right span {
  display: inline-block;
  font-size: 15px;
  margin-left: 10px;
  vertical-align: top;
}
.topic .topic-content .goRating .left .icon-like.active {
  color: #f00;
}
.topic .topic-content .topic-ratings {
  background: #f2f4f7;
}
.topic .writerating-wrapper {
  width: 100%;
  position: fixed;
  bottom: 0;
}


