
.like-card {
  margin: 0 auto;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 15px 30px;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 3px 0px #ccc;
  background: #fff;
  margin-bottom: 10px;
}
.like-card .card-header {
  height: 40px;
  line-height: 40px;
}
.like-card .card-header img {
  border-radius: 50%;
}
.like-card .card-header span {
  display: inline-block;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  vertical-align: top;
  color: #8e9ba2;
}
.like-card .card-content h2 {
  margin: 10px 0;
  font-size: 18px;
  line-height: 30px;
  font-weight: 800;
}
.like-card .card-content .card-info {
  font-size: 0;
  color: #8e9ba2;
}
.like-card .card-content .card-info span {
  display: inline-block;
  font-size: 13px;
}
.like-card .card-tab {
  position: absolute;
  top: 30px;
  right: 30px;
}


