
.topic-list {
  margin: 56px 0;
  background: #f2f4f7;
}
.topic-list ul li {
  position: relative;
  padding: 16px;
  box-sizing: border-box;
  background: #fff;
  border-top: 2px solid #f2f3f4;
  border-bottom: 1px solid #efeff0;
  box-shadow: 1px 1px 1px #c3c4c7;
  margin-bottom: 7px;
}
.topic-list ul li.good {
  border-left: 2px solid #1e8ae8;
}
.topic-list ul li.top {
  background: #f7f7f7;
}
.topic-list ul li .topic-user {
  height: 20px;
  font-size: 0;
}
.topic-list ul li .topic-user img {
  border-radius: 50%;
}
.topic-list ul li .topic-user .user-name {
  display: inline-block;
  color: #8e9ba2;
  font-size: 14px;
  line-height: 20px;
  vertical-align: top;
  margin-left: 10px;
}
.topic-list ul li h1 {
  margin: 10px 0;
  font-size: 17px;
  font-weight: bold;
  line-height: 25px;
}
.topic-list ul li .topic-info {
  font-size: 0;
  color: #8e9ba2;
}
.topic-list ul li .topic-info span {
  display: inline-block;
  font-size: 13px;
}
.topic-list ul li .topic-label {
  position: absolute;
  width: 30px;
  height: 18px;
  border-radius: 2px;
  background: #f8f8f8;
  top: 16px;
  right: 16px;
  text-align: center;
}
.topic-list ul li .topic-label span {
  line-height: 14px;
  font-size: 6px;
  color: #7d8b93;
}


