
header {
  height: 55px;
  background: #1e8ae8;
  border-bottom: 1px solid #0982eb;
  box-shadow: 0 1px 10px 4px #98b8d3;
}
header ul {
  display: -ms-flexbox;
  display: flex;
  font-size: 16px;
  line-height: 56px;
  text-align: center;
}
header ul li {
  -ms-flex: 1;
      flex: 1;
}
header ul li.active {
  border-bottom: 2px solid #fff;
}
header ul li a {
  color: #89c2f3;
}
header ul li a.active {
  color: #fff;
}
header .left {
  float: left;
  height: 100%;
  line-height: 55px;
  padding-left: 20px;
}
header .left i {
  font-size: 18px;
  color: #fff;
}
header .left p {
  font-size: 18px;
  color: #fff;
  font-weight: 800;
}
header .header-info {
  box-sizing: border-box;
  float: left;
  width: 70%;
  height: 100%;
  padding: 10px 0;
  margin-left: 18px;
  color: #fff;
  line-height: 20px;
}
header .header-info p {
  font-size: 15px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
header .header-info p:last-child {
  font-size: 12px;
}


