
.nav {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  background: #fff;
  box-shadow: -1px 0 3px 4px #f1f1f1;
  font-size: 0;
}
.nav ul {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.nav ul .nav-item {
  display: inline-block;
  -ms-flex: 1;
      flex: 1;
  font-size: 25px;
  line-height: 50px;
  text-align: center;
}
.nav ul .nav-item .router-link-active {
  color: #0f88eb;
}
.nav ul .nav-item a {
  display: inline-block;
  width: 100%;
  height: 100%;
  color: #d4d4d4;
}


